import React, {Component} from 'react';
import { Table  } from 'reactstrap';
import Pagination from './Pagination';

class Oppgave extends Component {


    constructor(props) {
        super(props);

        this.state = {
            items: [],
            pageOfItems: []
        };

        this.onChangePage = this.onChangePage.bind(this);

    }

    componentDidMount() {
        fetch('https://api.github.com/search/repositories?q=language:javascript&sort=stars&order=desc&per_page=100')
          .then(response => response.json())
          .then(data => this.setState({ items: data.items }));
    }

    onChangePage(pageOfItems) {
        // update state with new page of items
        this.setState({ pageOfItems: pageOfItems });
    }

    render() {

        return (
            <div>
                <div className="container">
                    <Table responsive className="table-hover">
                    <thead>
                      <tr className='text-center'>
                        <th>ID</th>
                        <th>Name</th>
                        <th>URL</th>
                        <th>Open issues</th>
                      </tr>
                    </thead>
                    <tbody>
                    {this.state.pageOfItems.map((item, i) => 
                        <tr key={i} className="text-center">
                            <th scope="row">{item.id}</th>
                            <td>{item.name}</td>
                            <td>{item.owner.url}</td>
                            <td>{item.open_issues}</td>
                        </tr>
                    )}
                    </tbody>
                  </Table>
                  <Pagination items={this.state.items} onChangePage={this.onChangePage} />
                </div>
            </div>
        );
    }
}

                      

export default Oppgave;


